import React , { Component, createRef} from 'react'
import {
    Form, Checkbox, Input, Select, Button 
} from 'antd';
import Submit from './submit'


const { Option } = Select;
const { TextArea } = Input;

class FormApp extends Component {
  formRef = createRef()

    constructor(props){
        super(props)
        this.state = {
            studentname:'',
            parentname:'',
            pregunta1:'',
            pregunta2:'',
            pregunta3:'',
            pregunta4:'',
            pregunta5:'',
            comentario1:'',
            comentario2:'',
            comentario3:'',
            

        }
        this.service = new Submit();
    }
    
    
    
     handleSubmit = () => {
       
        
        
            const studentname = this.state.studentname
            const parentname = this.state.parentname
            const pregunta1 = this.state.pregunta1
            const pregunta2 = this.state.pregunta2
            const pregunta3 = this.state.pregunta3
            const pregunta4 = this.state.pregunta4
            const pregunta5 = this.state.pregunta5
            const comentario1 = this.state.comentario1
            const comentario2 = this.state.comentario2
            const comentario3 = this.state.comentario3
            console.log('Ya se sometio')
            this.service.submit(studentname, parentname, pregunta1, pregunta2, pregunta3, pregunta4,pregunta5, comentario1, comentario2, comentario3)
            .then( response =>{
                this.setState({
                    studentname:'',
                    parentname:'',
                    pregunta1:'',
                    pregunta2:'',
                    pregunta3:'',
                    pregunta4:'',
                    pregunta5:'',
                    comentario1:'',
                    comentario2:'',
                    comentario3:''

                })
                console.log(response)
                this.formRef.current.resetFields()
                //this.formRef.current.resetFields();
                
            })
            .catch(error => console.log(error))
       
    }
    
    handleChange = (e) =>{
        const {name, value} = e.target;
        this.setState({[name]:value})

    }
    handleChangeSelect = (value)=>{
      console.log('Holarespeta1')
      this.setState({pregunta1:value})
    }
    handleChangeSelect2 = (value)=>{
      
      this.setState({pregunta2:value})
    }
    handleChangeSelect3 = (value)=>{
      
      this.setState({pregunta3:value})
    }
    handleChangeSelect4 = (value)=>{
      
      this.setState({pregunta4:value})
    }
    handleChangeSelect5 = (value)=>{
      
      this.setState({pregunta5:value})
    }
    
    ChangeCheck=(checkedValues)=>{
      
      var aux = ''
      checkedValues.forEach(element => aux += element+' ');
      this.setState({comentario1:aux})
      
    }
    render(){
      
    const formItemLayout = {
      labelCol: {
        xs: { span: 24 },
        sm: { span: 8 },
      },
      wrapperCol: {
        xs: { span: 24 },
        sm: { span: 16 },
      },
    };
    const tailFormItemLayout = {
      wrapperCol: {
        xs: {
          span: 24,
          offset: 0,
        },
        sm: {
          span: 16,
          offset: 8,
        },
      },
    };
    
    const options = [
      { label: 'Círculo', value: 'Circulo' },
      { label: 'Óvalo', value: 'Ovalo' },
      { label: 'Triángulo', value: 'Triangulo' },
      { label: 'Trapecio', value: 'Trapecio' },
      { label: 'Rectángulo', value: 'Rectangulo' },
      { label: 'Cuadrado', value: 'Cuadrado' },
      { label: 'Rombo', value: 'Rombo' }
    ];
    

        return(
            <div id ="Formulario">
                <Form {...formItemLayout} ref={this.formRef} onFinish={this.handleSubmit} id="formulario"  >
                <Form.Item
                {...formItemLayout}
                label={(
                    <span>
                        Nombre del alumno&nbsp;
                        
                    </span>
                )}
                name = "studentname" rules= {[{ required: true, message: 'Proporciona el nombre del alumno', whitespace: true }]}>
                <Input name="studentname" value={this.state.studentname} onChange={ e => this.handleChange(e)}/>
              </Form.Item>

            <Form.Item
                {...formItemLayout}
                label={(
                    <span>
                        Nombre del padre o tutor&nbsp;
                        
                    </span>
                )}
                name = "parentname" rules= {[{ required: true, message: 'Proporciona el nombre del padre o tutor', whitespace: true }]}>
                
            <Input name="parentname" value={this.state.parentname} onChange={e => this.handleChange(e)}/>
     

            </Form.Item>

        <Form.Item
          
          label="Se apoya de un letrero para escribir su nombre:"
          name = "pregunta1" rules= {[{ required: true, message: 'Selecciona una respuesta', whitespace: true }]}>
        
         
            
                <Select  name="pregunta1" 
                value= {this.state.pregunta1}
                onChange={ this.handleChangeSelect}>
                    
                    <Option value="si">Si</Option>
                    <Option value="no">No</Option>
                    <Option value="ayuda" disabled>Necesita ayuda</Option>                    
                </Select>
        
        </Form.Item>

        <Form.Item
          {...formItemLayout}
          label="Escribe su nombre con grafias convencionales (escritura normal)"
          name = "pregunta2" rules= {[{ required: true, message: 'Selecciona una respuesta', whitespace: true }]}>
         
            
                <Select id="pregunta2" name="pregunta2" 
                value= {this.state.pregunta2}
                onChange={ this.handleChangeSelect2}>
                    <Option value="">Selecciona una respuesta</Option>
                    <Option value="si">Si</Option>
                    <Option value="no">No</Option>
                    <Option value="ayuda">Necesita ayuda</Option>                    
                </Select>
         
        </Form.Item>

        <Form.Item
          {...formItemLayout}
          label="Se muestra motivado al realizar su propia obra de arte"
          name = "pregunta3" rules= {[{ required: true, message: 'Selecciona una respuesta', whitespace: true }]}>
        
          
            
                <Select id="pregunta3" name="pregunta3" 
                value= {this.state.pregunta3}
                onChange={this.handleChangeSelect3}>
                    <Option value="">Selecciona una respuesta</Option>
                    <Option value="si">Si</Option>
                    <Option value="no">No</Option>
                    <Option value="ayuda" disabled>Necesita ayuda</Option>                    
                </Select>
         
        </Form.Item>

        <Form.Item
          {...formItemLayout}
          label="Al realizar su obra de arte expresa lo que le provoca:"
          name = "Pregunta4" rules= {[{ required: true, message: 'Selecciona una respuesta', whitespace: true }]}>
        
        
            
                <Select id="pregunta4" name="pregunta4" 
                value= {this.state.pregunta4}
                onChange={ this.handleChangeSelect4}>
                    <Option value="">Selecciona una respuesta</Option>
                    <Option value="si">Si</Option>
                    <Option value="no">No</Option>
                    <Option value="ayuda" disabled>Necesita ayuda</Option>                    
                </Select>
          
        </Form.Item>
        <Form.Item
          {...formItemLayout}
          label="Sabe para que sirve la gráfica"
          name = "Pregunta5" rules= {[{ required: true, message: 'Selecciona una respuesta', whitespace: true }]}>
        
        
            
                <Select id="pregunta5" name="pregunta5" 
                value= {this.state.pregunta5}
                onChange={ this.handleChangeSelect5}>
                    <Option value="">Selecciona una respuesta</Option>
                    <Option value="si">Si</Option>
                    <Option value="no">No</Option>
                    <Option value="ayuda" disabled>Necesita ayuda</Option>                    
                </Select>
          
        </Form.Item>
        <Form.Item
          {...formItemLayout}
          label="Al momento de registrar que se le dificulta "
          name = "comentario3" rules= {[{ required: true, message: 'Escribe una respuesta', whitespace: true }]}>
       
         
                <TextArea name='comentario3' value = {this.state.comentario3} onChange={ e => this.handleChange(e)} rows={4} />
                
         
        </Form.Item>

        
        <Form.Item
          {...formItemLayout}
          label="Selecciona las figuras que identifica "
          name = "comentario1" rules= {[{ required: true, message: 'Selecciona al menos una respuesta' }]}>
       
                
              <Checkbox.Group options={options} value = {this.state.comentario1} onChange={ this.ChangeCheck}/>
       
        </Form.Item>

        <Form.Item
          {...formItemLayout}
          label="¿Se observa coherencia al narral el cuento? ¿Qué se le dificulta? "
          name = "comentario2" rules= {[{ required: true, message: 'Escribe una respuesta', whitespace: true }]}>
       
         
                <TextArea name='comentario2' value = {this.state.comentario2} onChange={ e => this.handleChange(e)} rows={4} />
                
         
        </Form.Item>
        <Form.Item
          {...formItemLayout}
          label="Observaciones por parte de la maestra "
          name = "piña" rules= {[{ required: false, message: 'Escribe una respuesta' }]}>
       
         
                <TextArea disabled name='comentariopiña'  onChange={ e => this.handleChange(e)} rows={4} />
                
         
        </Form.Item>

        <Form.Item {...tailFormItemLayout}>
          <Button className="login-form-button" type="primary" htmlType="submit" >Enviar</Button>
          
        </Form.Item>

        
          </Form>






                
            </div>
        )
    }
}


export default FormApp;