import axios from 'axios';

class Submit{
    constructor (){
        let service = axios.create({
            baseURL:"https://formulario90.herokuapp.com/api",
            withCredentials:false
        })
        this.service= service
    }
    submit = (studentname, parentname, pregunta1, pregunta2, pregunta3, pregunta4,pregunta5, comentario1, comentario2,comentario3)=>{
        console.log(studentname, parentname, pregunta1, pregunta2, pregunta3, pregunta4, comentario1, comentario2)
        return this.service.post('/submit', {studentname, parentname, pregunta1, pregunta2, pregunta3, pregunta4,pregunta5, comentario1, comentario2,comentario3})
        .then(response =>response.data)
    }


}
export default Submit;